import { CardActions as MUICardActions, IconButton } from "@material-ui/core"
import { ExpandMore, PhonelinkRing, Room } from "@material-ui/icons"
import React from "react"
import styled from "styled-components"

const SCardActions = styled((props) => <MUICardActions { ...props } />)`
   && {
        padding: 5px 10px 10px 10px;
   }
`

const CardActions: React.FC = (props) => {
    const { children } = props

    return (
        <SCardActions>
            <IconButton aria-label="add to favorites" size="small">
                <PhonelinkRing />
            </IconButton>
            <IconButton aria-label="share" size="small">
                <Room />
            </IconButton>

            { children }
        </SCardActions>
    )
}

export default CardActions
