import { Dispatch } from "redux"
import closePdf from "../../../pdf.js/closeDocument"
import openPdf from "../../../pdf.js/openDocument"
import { CLOSE_APP, OPEN_APP, SET_DOCUMENT_TYPE } from "./types"

export const SetDocumentType = (documentType: string) => ({
    documentType,
    type: SET_DOCUMENT_TYPE,
})

export const OpenApp = () => ({
    type: OPEN_APP,
})

export const CloseApp = () => ({
    type: CLOSE_APP,
})

export const OpenNewDocument = (type: string, source: string) =>
    (dispatch: Dispatch, getState: any) => {
        // open the new doc async
        const openDocs = async () => {
            if (type === "pdf") {
                await closePdf()
                await openPdf(source)
            }

            dispatch(SetDocumentType(type))
        }

        return openDocs()

        // reset the app state
    }
