import { ArrowBack, ArrowForward, Close, Folder } from "@material-ui/icons"
import React, { useCallback, useEffect, useLayoutEffect, useRef, useState } from "react"
import { MapDispatchToProps } from "react-redux"
import { connect } from "react-redux"
import { RouteComponentProps, withRouter } from "react-router"
import { AnyAction } from "redux"
import { ThunkDispatch } from "redux-thunk"
import { disableUi, enableUi } from "../../redux/actions"
import { AppState } from "../../redux/root"
import { resetAll } from "../app_store/redux/actions"
import DocsItem from "./DocItem"
import { CloseApp, OpenApp, OpenNewDocument } from "./redux/actions"
import { SCButton, SCDocMenu, SCMenuBody } from "./styles"

interface IOwnProps extends RouteComponentProps { }

interface IStateProps {
    appIsOpen: boolean
}

interface IDispatchProps {
    openDoc: (type: string, source: string) => void
    opnApp: () => void
    clsApp: () => void
    reset: () => void
    disblUi: () => void
    enblUi: () => void
}

type Props = IOwnProps & IStateProps & IDispatchProps

const mapStateToProps = (state: AppState, ownProps: IOwnProps): IStateProps => ({
    appIsOpen: state.appIsOpen,
})


const mapDispatchToProps: MapDispatchToProps<IDispatchProps, {}> = (
    dispatch: ThunkDispatch<{}, {}, AnyAction>,
) => ({
    clsApp: () => dispatch(CloseApp()),
    openDoc: (type: string, source: string) => dispatch(OpenNewDocument(type, source)),
    opnApp: () => dispatch(OpenApp()),
    reset: () => dispatch(resetAll()),
    disblUi: () => dispatch(disableUi()),
    enblUi: () => dispatch(enableUi()),
})

const urls = [
    {
        name: "Moris Venture",
        url: "/pdfs/moris_venture.pdf",
    },
    {
        name: "FULL_DOC_USE_CASES_HIGHLIGHTED",
        url: "/pdfs/FULL_DOC_USE_CASES_HIGHLIGHTED.pdf",
    },
    {
        name: "Resignation_process_Role_code_Contract_number.pdf",
        url: "/pdfs/Resignation_process_Role_code_Contract_number.pdf",
    },
    {
        name: "Refund_inquiry_Purchase_order_Invoice.pdf",
        url: "/pdfs/Refund_inquiry_Purchase_order_Invoice.pdf",
    },
    {
        name: "Purchase_order_Invoice_Administration.pdf",
        url: "/pdfs/Purchase_order_Invoice_Administration.pdf",
    },
    {
        name: "Invoice_Bill_of_lading_Finance_Administration",
        url: "/pdfs/Invoice_Bill_of_lading_Finance_Administration.pdf",
    },
    {
        name: "Contact_recommendation_Person_Company.pdf",
        url: "/pdfs/Contact_recommendation_Person_Company.pdf",
    },
]

const DocsMenu: React.FC<Props> = (props) => {
    const { openDoc, history, appIsOpen, opnApp, clsApp, reset, disblUi, enblUi } = props
    const offsetLeft = -211
    const [ isOpen, setIsOpen ] = useState(false)
    const [ left, setLeft ] = useState(offsetLeft)
    const [ openAppBtnDisabled, setOpenAppBtnDisabled ] = useState(false)
    const menuEl = useRef(document.createElement("div"))

    const open = () => {
        setIsOpen(true)
        setLeft(0)
    }

    const clsCbk = useCallback(() => {
        setIsOpen(false)
        setLeft(offsetLeft)
    }, [ offsetLeft ])

    const close = () => {
        setIsOpen(false)
        setLeft(offsetLeft)
    }

    const openMenu = () => {
        !isOpen ? open() : close()
    }

    const openAppHandler = () => {
        appIsOpen ? clsApp() : opnApp()
    }

    useEffect(() => {
        const { innerWidth } = window
        if (innerWidth > 1024) {
            setOpenAppBtnDisabled(true)
            opnApp()
        }
    }, [ setOpenAppBtnDisabled, opnApp ])

    useLayoutEffect(() => {
        const clickOutside = (ev: MouseEvent) => {
            if (menuEl.current && !menuEl.current.contains(ev.target as Node)) {
                clsCbk()
            }
        }

        document.addEventListener("mousedown", clickOutside)

        return () => {
            document.removeEventListener("mousedown", clickOutside)
        }
    }, [ clsCbk ])

    return (
        <SCDocMenu ref={ menuEl }>
            <SCButton onClick={ openMenu }>
                { !isOpen ? <Folder /> : <Close /> }
            </SCButton>

            { !openAppBtnDisabled && <SCButton onClick={ openAppHandler }>
                { !appIsOpen ? <ArrowBack /> : <ArrowForward /> }
            </SCButton>
            }

            <SCMenuBody left={ left }>
                { urls.map((url: any, i: number) =>
                    <DocsItem clickHandler={ () => {
                        const load = async () => {
                            disblUi()
                            await openDoc("pdf", url.url)
                            await reset()
                            enblUi()
                            close()
                            // history.push("/")
                        }
                        load()
                    } } key={ i }>{ url.name }</DocsItem>) }
            </SCMenuBody>

        </SCDocMenu>

    )
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DocsMenu))
