import { CLOSE_APP, DocumentActions, IDocumentState, OPEN_APP, OpenCloseApp, SET_DOCUMENT_TYPE } from "./types"

const initialState: IDocumentState = {
    type: "",
}

export const documentReducer = (state: IDocumentState = initialState, action: DocumentActions) => {
    switch (action.type) {
        case SET_DOCUMENT_TYPE:
            return Object.assign({}, state, { type: action.documentType })


        default:
            return state
    }
}

export const appIsOpenReducer = (state: boolean = false, action: OpenCloseApp) => {
    switch (action.type) {
        case OPEN_APP:
            return true

        case CLOSE_APP:
            return false

        default:
            return state
    }
}
