import React from "react"
import styled from "styled-components"

const SView = styled.div`
    position: relative;

    height: 100vh;
    width: calc(100% - 70px);

    @media screen and (max-width: 1024px) {
        width: calc(100% - 60px);
    }
`

const View: React.FC<{}> = (props) => {
    return (
        <SView>
            { props.children }
        </SView>
    )
}

export default View
